
#profile-details{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-bottom: 2rem;
    box-shadow: 1px 1px 5px rgba(0,0,0,0.1);
}
#profile-photo{
    width: 20%;
    object-fit: contain;
}
#profile-details-container{
    width: 60%;
    height: 60%;
    padding: 5%;
    text-align: center;
}

.big-hr{
    width: 90% !important;
}