@import url('https://fonts.googleapis.com/css?family=Lato:400,400i,700,700i,900');
@import url('https://fonts.googleapis.com/css?family=Roboto:400,500,700,900');
@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,400,400i,700');

body{
  margin: 0;
  padding:0;
  font-family: Lato, Roboto, "Helvetica Neue", Helvetica, Arial;

}
form {
  padding: 10px;
  border: none;
  border-bottom: 2px;
}
p,h1,h2,h3,h4,h5{
  opacity: .5;
}
input[type=text], input[type=email], input[type=password], input[type=submit], input[type=date], input[type=time]{
  border: none;
  border-bottom: 1px solid rgba(0,0,0,0.2);
  margin : 1%;
  transition: 1s;
  background: rgba(255,255,255,0.3);
  box-shadow: 1px 1px 5px rgba(255,255,255,0.1);
  border-radius: 20px;
  padding: 10px;

  /* hola pistachitos one more time */
    box-shadow: 0px 2px 33px 0px rgba(0,0,0,0.1);
    padding: 16px;
    border-width: 0;
    font-weight: 600;
    font-size: 13px;
    color: #bcbcbc!important;
    text-align: left;
}
input[type=submit]{
  color: #ECB84C;
  font-weight: bold;
  border: 1px solid rgba(0,0,0,0.5);
  width: 30%;
}

input[type=text]:focus, input[type=email]:focus, input[type=password]:focus, input[type=submit]:focus, input[type=date]:focus, input[type=time]:focus, input[type=file]:focus{ 
  outline: none !important;
  box-shadow: 0px 0px 5px #ECB84C;
  /* -webkit-transition: .3s all ease; */
  animation: 1s;
  transition-duration: 1s;
}
input[type=file]{
  max-width: 80% !important;
  font-size: 1.25rem !important;
  font-weight: 700 !important;
  text-overflow: ellipsis !important;
  white-space: nowrap !important;
  cursor: pointer !important;
  display: inline-block !important;
  overflow: hidden !important;
  padding: 0.625rem 1.25rem !important;
}
/* input[type=text]:hover, input[type=email]:hover, input[type=password]{
  transition: 1s;
  animation-duration: 1s;
} */
h2{
  margin:0;
  font-size: 1.7em;
}
h1{
  font-size: 2em;
}
.form-container label {
  text-transform: uppercase;
  text-align: left;
  padding-bottom: .5rem;
  padding-left: 1rem;
  opacity: .5;
}


/* HOLA PISTACHITOS */
button, input[type="submit"]{
  background: #fff;
    outline: none;
    color: #ECB84C!important;
    padding: 14px 38px!important;
    font-size: 13px!important;
    letter-spacing: 2px!important;
    font-weight: 700!important;
    text-transform: uppercase;
    text-align: center;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    border-radius: 30px;
    -webkit-box-shadow: 0 10px 50px rgba(50,50,93,.15);
    box-shadow: 0 10px 50px rgba(50,50,93,.15);
    -webkit-animation: fadein 5s;
    animation: fadein 5s;
    cursor: pointer;
    border: none;
    width: fit-content;
}

button:active, input[type="submit"]:active {
  outline: 0px;
}

button:hover, input[type="submit"]:hover {
  background: #ECB84C;
  color: #fff!important;
  opacity: 1!important;
  -webkit-box-shadow: 0 10px 50px rgba(50,50,93,.25)!important;
  box-shadow: 0 10px 50px rgba(50,50,93,.25)!important;
}
.input-fix{
  background: #fff;
    outline: none;
    color: #ECB84C!important;
    font-size: 13px!important;
    letter-spacing: 2px!important;
    font-weight: 700!important;
    text-transform: uppercase;
    text-align: center;
    -webkit-transition: .3s all ease;
    -o-transition: .3s all ease;
    transition: .3s all ease;
    border-radius: 30px;
    -webkit-box-shadow: 0 10px 50px rgba(50,50,93,.15);
    box-shadow: 0 10px 50px rgba(50,50,93,.15);
    -webkit-animation: fadein 5s;
    animation: fadein 5s;
    cursor: pointer;
    border: none;
    width: fit-content;
}
